<template>
  <div class="">
    <span class="loader" v-if="productLoading"></span>
    <span class="" v-else-if="!productsData">Не удалось загрузить товар</span>
    <main class="content container" v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }"
              >Каталог</router-link
            >
          </li>
          <li class="breadcrumbs__item">
            <!-- @click.prevent="$router.push({name: 'main'})" способ програмно переключать страницы -->
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">{{
              category.title
            }}</router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> {{ product.title }} </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img
              :src="product.image.file.url"
              :alt="product.title"
            />
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">{{ product.id }}</span>
          <h2 class="item__title">{{ product.title }}</h2>
          <div class="item__form">
            <form
              class="form"
              action="#"
              method="POST"
              @submit.prevent="addToCart"
            >
              <b class="item__price"> {{ product.price | numberFormat }} ₽ </b>

              <fieldset class="form__block">
                <legend class="form__legend">Цвет:</legend>
                <ul class="colors">
                  <li class="colors__item">
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        name="color-item"
                        value="blue"
                        checked=""
                      />
                      <span
                        class="colors__value"
                        style="background-color: #73b6ea"
                      >
                      </span>
                    </label>
                  </li>
                  <li class="colors__item">
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        name="color-item"
                        value="yellow"
                      />
                      <span
                        class="colors__value"
                        style="background-color: #ffbe15"
                      >
                      </span>
                    </label>
                  </li>
                  <li class="colors__item">
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        name="color-item"
                        value="gray" />
                      <span
                        class="colors__value"
                        style="background-color: #939393"
                      >
                      </span
                    ></label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Объемб в ГБ:</legend>

                <ul class="sizes sizes--primery">
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="32"
                      />
                      <span class="sizes__value"> 32gb </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="64"
                      />
                      <span class="sizes__value"> 64gb </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="128"
                        checked=""
                      />
                      <span class="sizes__value"> 128gb </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <div class="item__row">
                <div class="form__counter">
                  <button
                    type="button"
                    aria-label="Убрать один товар"
                    @click="productAmount--"
                  >
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-minus"></use>
                    </svg>
                  </button>

                  <input type="text" v-model="productAmount" />

                  <button
                    type="button"
                    aria-label="Добавить один товар"
                    @click="productAmount++"
                  >
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-plus"></use>
                    </svg>
                  </button>
                </div>

                <button class="button button--primery" type="submit" :disabled="productAddSending">
                  В корзину
                </button>
              </div>

              <div class="" v-show="productAdded">Товар добавлен в корзину</div>
              <div class="" v-show="productAddSending">Добавляем товар в корзину...</div>

            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a class="tabs__link tabs__link--current"> Описание </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#"> Характеристики </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#"> Гарантия </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#"> Оплата и доставка </a>
            </li>
          </ul>

          <div class="item__content">
            <p>
              Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br />
              Синхронизация со смартфоном<br />
              Связь по Bluetooth Smart, ANT+ и Wi-Fi<br />
              Поддержка сторонних приложений<br />
            </p>

            <a href="#"> Все характеристики </a>

            <h3>Что это?</h3>

            <p>
              Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет
              оптимизировать свои велотренировки, сделав их максимально
              эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками
              поANT+, объединяя полученную с них информацию. Данные отображаются
              на дисплее, а также сохраняются на смартфоне. При этом на
              мобильное устройство можно установить как фирменное приложение,
              так и различные приложения сторонних разработчиков. Велокомпьютер
              точно отслеживает местоположение, принимая сигнал с целого
              комплекса спутников. Эта информация позволяет смотреть уже
              преодоленные маршруты ипланировать новые велопрогулки.
            </p>

            <h3>Дизайн</h3>

            <p>
              Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры
              устройства составляют всего 74,6 x 47,3 x 22,1 мм. что не
              превышает габариты смартфона. Корпус гаджета выполнен из черного
              пластика. Наобращенной к пользователю стороне расположен дисплей
              диагональю 56 мм. На дисплей выводятся координаты и скорость, а
              также полученная сосмартфона и синхронизированных датчиков
              информация: интенсивность, скорость вращения педалей, пульс и т.д.
              (датчики не входят в комплект поставки). Корпус велокомпьютера
              имеет степень защиты от влаги IPX7. Этоозначает, что устройство не
              боится пыли, а также выдерживает кратковременное (до 30 минут)
              погружение в воду на глубину не более 1метра.
            </p>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
// import products from '@/data/products';
// import categories from '@/data/categories';
import goToPage from "@/helpers/goToPage";
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import { API_BASE_URL } from "@/config";
import {mapActions} from 'vuex'

export default {
  // props: ['pageParams'],
  data() {
    return {
      productAmount: 1,
      productsData: null,
      productLoading: false,
      productError: false,
      productAdded: false,
      productAddSending: false
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      // +this.$route.params.id плюс значит привод к числу строки
      // return products.find(product => product.id === +this.$route.params.id)
      return this.productsData;
    },
    category() {
      return this.productsData.category;
      // return categories.find(category => category.id === this.product.categoryId)
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    goToPage,
    addToCart() {
      this.productAdded = false
      this.productAddSending = true
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      })
      .then(() => {
        this.productAdded = true
        this.productAddSending = false
      })
    },
    loadProduct() {
      clearTimeout(this.loadingTimer)
      this.productLoading = true;
      this.productError = false;
      this.loadingTimer = setTimeout(() => {
        axios
        .get(API_BASE_URL + "/api/products/" + this.$route.params.id)
        .then((response) => (this.productsData = response.data))
        .catch(() => (this.productError = true))
        .then(() => (this.productLoading = false));
      },2000);
    },
  },
  // created() {
  //   this.loadProduct();
  // },
  watch: {
    "$route.params.id": {
      handler() {
        this.loadProduct()
      },
      immediate: true
    }
  },
};
</script>
